
@font-face {
  font-family: "Roboto Condensed";
  font-display: swap;
  src: local("Roboto Condensed"),
    url("../fonts/roboto-condensed-v18-cyrillic_latin-700.woff2") format("woff2"),
    url("../fonts/roboto-condensed-v18-cyrillic_latin-700.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Roboto Condensed";
  font-display: swap;
  src: local("Roboto Condensed"),
    url("../fonts/roboto-condensed-v18-cyrillic_latin-regular.woff2") format("woff2"),
    url("../fonts/roboto-condensed-v18-cyrillic_latin-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Roboto Condensed";
  font-display: swap;
  src: local("Roboto Condensed"),
    url("../fonts/RobotoCondensed-Light.woff2") format("woff2"),
    url("../fonts/RobotoCondensed-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "Bebas Neue";
  font-display: swap;
  src: local("Bebas Neue"),
    url("../fonts/BebasNeue-Regular.woff2") format("woff2"),
    url("../fonts/BebasNeue-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Bebas Neue";
  font-display: swap;
  src: local("Bebas Neue"),
    url("../fonts/BebasNeue-Bold.woff2") format("woff2"),
    url("../fonts/BebasNeue-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

